.players li {
    display: flex;
    align-items: center;
    width: 50%;
    border: 2px solid transparent;
}



.player-button {
    width: 3rem;
    border: none;
    background: none;
    color: #c3ba78;
    font-size: 0.9rem;
    cursor: pointer;
    transition: color 0.2s;
    padding: 0.5rem 0.25rem 0.25rem 0.25rem;
    text-align: center;
}

.player-button:hover {
    color: #f8ca31;
}

.player {
    border: 2px solid transparent;
    padding: 0.5rem;
    border-radius: 4px;
    font-weight: bold;
}

.player-name {
    display: inline-block;
    width: 10rem;
    font-size: 1rem;
    color: #e1dec7;
    text-transform: uppercase;
    margin: 0;
    padding: 0.5rem;
    border-radius: 4px;
    text-overflow: ellipsis;
    text-align: center;
}

.player input {
    font: inherit;
    font-size: 1rem;
    width: 10rem;
    border: none;
    padding: 0.5rem;
    animation: pulse-text 2s infinite;
    background-color: #46432f;
    text-align: center;
    text-transform: uppercase;
}

.player-symbol {
    margin-left: 1rem;
    font-size: 1rem;
    color: #e1dec7;
}

.players {
    list-style: none;
    padding: 0;
    margin: 1rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;

}

ol {
    list-style: none;
    margin: 0;
    padding: 0;
  }

.active {
    border-color: #f6e35a;
    animation: pulse 2s infinite ease-in-out;
}

